import React from 'react';
import { NavLink } from "react-router-dom";

function Navbar() {
    return (
        <>
            <div className="container-fluid">
                <div className="row">
                    {/* <div className="col-12 mx-auto"> */}

                        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                            <div className="container-fluid">
                                <NavLink className="navbar-brand text-warning" to="/">My Crud App</NavLink>
                                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon"></span>
                                </button>

                                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                    <ul className="navbar-nav mb-2 mb-lg-0">
                                        {/* <li className="nav-item">
                                            <NavLink exact className="nav-link" aria-current="page" to="/">Home</NavLink>
                                        </li> */}
                                        <li className="nav-item">
                                            <NavLink className="nav-link" to="/add">Add User</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink className="nav-link" to="/all">All Users</NavLink>
                                        </li>

                                    </ul>
                                </div>
                            </div>
                        </nav>
                    {/* </div> */}
                </div>
            </div>
        </>
    )
}

export default Navbar
